.errorBoundary {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  color: #721c24;
  border-radius: 8px;
}

.errorBoundaryContent {
  max-width: 600px;
  margin: 0 auto;
}

.errorBoundary div {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  max-width: 600px;
}

.errorBoundary h1 {
  font-size: 2rem;
  margin-bottom: 10px;
}

.errorBoundary p {
  font-size: 1.2rem;
  margin-bottom: 20px;
}

.errorBoundary button {
  background-color: #721c24;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
  text-transform: uppercase;
  max-width: 250px;
  margin: 0 auto;
}

.errorBoundary button:hover {
  background-color: #5a1d22; /* Темнее красный при наведении */
}
